import dayjs from 'dayjs';
import Loading from '../components/Loading';
import Pagination from '@/components/Pagination';
import Error from '@/components/Error';
export default {
    name: 'PageSearch',
    components: {
        Pagination,
        Loading,
        Error
    },
    transition: {
        name: 'search',
        mode: 'out-in'
    },
    head() {
        return Object.assign(this.$nuxtI18nSeo(), {
            title: `${
                this.params.page
                    ? `${this.$t('page', { page: this.params.page })} - `
                    : ''
            } 关键字 ${this.params.keyword} 的搜索结果  - ${process.env.title}`
        });
    },
    data() {
        return {
            pagination: 1,
            keyword: '',
            loading: false,
            error: null,
            start: false
        };
    },
    computed: {
        params() {
            const params = this.$route.params;
            return {
                page: params.page,
                keyword: params.keyword
                    ? params.keyword.replace(/\.html$/, '')
                    : ''
            };
        }
    },
    mounted() {
        if (this.params.keyword) {
            this.getData();
            this.keyword = this.params.keyword;
            if (this.params.page) {
                this.pagination = parseInt(this.params.page);
            }
        }
    },
    methods: {
        formatDate(date, params) {
            if (params) {
                return dayjs(date).format(params);
            } else {
                return dayjs().from(dayjs(date));
            }
        },
        handleLink(page) {
            if (page <= 1) {
                return `/search/${this.keyword.trim()}.html`;
            } else {
                return `/search/${this.keyword.trim()}/${page}.html`;
            }
        },
        search(page, event) {
            if (event) {
                if (this.loading) return false;
                if (!this.keyword.trim()) return alert('请输入要搜索的关键词');
            }
            this.pagination = page;
            if (page === 1) {
                this.$router.push(
                    this.localePath(`/search/${this.keyword.trim()}.html`)
                );
                return false;
            }
            if (
                page > 1 &&
                page <= this.$store.state.data.search_posts.total_pages
            ) {
                this.$router.push(
                    this.localePath(
                        `/search/${this.keyword.trim()}/${page}.html`
                    )
                );
            }
        },
        getData() {
            const { page, keyword } = this.params;
            this.loading = true;
            this.start = true;
            this.error = null;
            this.$store
                .dispatch('searchPosts', {
                    keyword,
                    page: page || 1
                })
                .then((e) => {
                    this.loading = false;
                })
                .catch((err) => {
                    this.error = err;
                    this.loading = false;
                });
        }
    }
};
