export default {
    name: 'Pagination',
    props: {
        pageSize: {
            type: Number,
            default: 0
        },
        totalPages: {
            type: Number,
            default: 0
        },
        currentPage: {
            type: Number,
            default: 0
        },
        handleLink: {
            type: Function,
            default: () => {}
        }
    },
    computed: {
        prevNum() {
            return this.currentPage - 1 || 1;
        },
        nextNum() {
            if (this.currentPage === this.totalPages) {
                return this.totalPages;
            } else {
                return this.currentPage + 1;
            }
        },
        btnNum() {
            if (this.totalPages < 4) {
                return [...Array(this.totalPages)].map((_, i) => i + 1);
            }
            if (this.currentPage < this.totalPages - 1) {
                if (this.currentPage - 2 === 1) {
                    return [
                        this.currentPage - 2,
                        this.currentPage - 1,
                        this.currentPage,
                        this.currentPage + 1
                    ];
                }
                if (this.currentPage === this.totalPages - 2) {
                    return [
                        this.currentPage - 1,
                        this.currentPage,
                        this.currentPage + 1,
                        this.currentPage + 2
                    ];
                } else if (this.currentPage === 1) {
                    return [
                        this.currentPage,
                        this.currentPage + 1,
                        this.currentPage + 2,
                        this.currentPage + 3
                    ];
                } else {
                    return [
                        this.currentPage - 1,
                        this.currentPage,
                        this.currentPage + 1,
                        this.currentPage + 2
                    ];
                }
            }
            if (this.currentPage >= this.totalPages - 1) {
                if (this.currentPage === this.totalPages) {
                    return [
                        this.currentPage - 3,
                        this.currentPage - 2,
                        this.currentPage - 1,
                        this.currentPage
                    ];
                }
                if (this.currentPage === this.totalPages - 1) {
                    return [
                        this.currentPage - 2,
                        this.currentPage - 1,
                        this.currentPage,
                        this.currentPage + 1
                    ];
                }
                return [
                    this.currentPage - 1,
                    this.currentPage,
                    this.currentPage + 1,
                    this.currentPage + 2
                ];
            }
            return [];
        },
        showFirst() {
            if (this.currentPage === 1) {
                return false;
            }
            if (this.currentPage - 1 === 1) {
                return false;
            }
            if (this.currentPage - 2 === 1) {
                return false;
            }
            return true;
        },
        showLast() {
            if (this.currentPage === this.totalPages) {
                return false;
            }
            if (this.currentPage + 1 === this.totalPages) {
                return false;
            }
            if (this.currentPage + 2 === this.totalPages) {
                return false;
            }
            return true;
        }
    },
    methods: {
        next() {
            if (this.totalPages) {
                if (this.currentPage === this.totalPages) return false;
            }
            this.$emit('change', this.currentPage + 1);
        },
        prev() {
            if (this.currentPage === 1) return false;
            this.$emit('change', this.currentPage - 1);
        }
    }
};
